<template>
    <div class="Box" ref="Box">
        <div ref="outer" id="WorldMap"></div>
        <div class="tooltips">
            <div>{{ tooltips.届数 + `${tooltips.Year}年${tooltips.主办城市}${tooltips.Season == "winter" ? "冬季" : "夏季"}奥运会` }}</div>
            <div>{{ tooltips.举办时间 }}</div>
            <div>{{ tooltips.举办国家 }} {{ tooltips.主办城市 }}</div>
            <div>{{ tooltips.口号 || "" }}</div>
            <router-link class="link" :to="encodeURI(`/奥运风采馆/奥运会介绍/每年奖牌趋势/${tooltips.Season}/${tooltips.Year}/Gold`)">查看详情</router-link>
            <div class="scale">
                <i :class="{ btn: true, 'el-icon-video-pause': !timeline.paused, 'el-icon-video-play': timeline.paused }" @click="pauseAndPlay" circle></i>
                <div>时间轴</div>
                <el-slider :min="3" :max="maxSize" class="slider" v-model="progress" :step="3" :format-tooltip="formatTooltip" @change="timeControl"> </el-slider>
            </div>
            <div class="scale">
                <div>地图大小</div>
                <el-slider :min="1" :max="5" class="slider" v-model="SVGScale" style="flex:1" :step="1" @change="SVGScaleChange"> </el-slider>
            </div>
        </div>
    </div>
</template>
<script>
    import anime from "animejs";
    export default {
        data() {
            return {
                maxSize: 0,
                progress: 0,
                allPoints: [],
                points: [],
                viewBox: [],
                svg: null,
                dataset: {},
                timeline: {},
                xLine: {},
                SVGScale: 5,
                yLine: {},
                tooltips: {
                    Year: 1896,
                    届数: "第01届",
                    赛事名称: "1896年雅典奥运会",
                    举办国家: "希腊",
                    主办城市: "雅典",
                    举办时间: "1896年04月06日-1896年04月15日",
                    口号: null,
                    Season: "Summer",
                    url: null,
                    name: null,
                    imgUrl: null,
                    text: "1896年",
                },
            };
        },
        watch: {
            "timeline.paused": function(val) {
                this.$notify({
                    type: "success",
                    title: val ? "暂停播放动画" : "开始播放动画",
                    position: "bottom-right",
                    duration: 1000,
                });
            },
        },
        async mounted() {
            this.dataset = await fetch("./奥运会赛事统一数据最终添加赛事介绍.json", { cache: "force-cache" }).then((res) => res.json());
            this.$refs.outer.innerHTML = await fetch("./worldMap.svg").then((res) => res.text());
            // 找到所有的点并排序
            let allPoints = [...this.$refs.outer.querySelectorAll("[data-name$=summer]"), ...this.$refs.outer.querySelectorAll("[data-name$=winter]")].sort(
                (a, b) => a.dataset.name.split("-")[0] - b.dataset.name.split("-")[0]
            );
            let points = allPoints.map((i) => {
                let [year, season] = i.dataset.name.split("-");
                i.style.opacity = 0;

                return { year, season };
            });

            this.maxSize = (allPoints.length - 1) * 3;

            this.xLine = this.$refs.outer.querySelector("line#x");
            this.yLine = this.$refs.outer.querySelector("line#y");

            this.points = points;
            this.allPoints = allPoints;

            this.svg = this.$refs.outer.children[0];
            // 布置文字
            this.text = document.createElementNS("http://www.w3.org/2000/svg", "text");
            this.text.classList.add("Text");
            this.svg.appendChild(this.text);
            this.text2 = document.createElementNS("http://www.w3.org/2000/svg", "text");
            this.text2.classList.add("Text2");
            this.svg.appendChild(this.text2);
            this.viewBox = this.svg.getAttribute("viewBox").split(" ");
            this.timelineSetting();
        },
        methods: {
            timelineSetting() {
                let that = this;
                this.timeline = anime.timeline();

                this.timeline.pause();
                this.allPoints.forEach((targets, index) => {
                    this.timeline.add({
                        targets,
                        scale: [1, 2, 1],
                        opacity: 0.7,
                        duration: 3000,
                        begin(anim) {
                            // 基础数据获取
                            let { x, y, width, height } = anim.animatables[0].target.getBBox();
                            let [, , width1, height1] = that.viewBox;
                            let xDis = x + width / 2;
                            let yDis = y + height / 2;

                            if (parseInt((xDis * 100) / width1)) {
                                let highlightX = (xDis * 100) / width1;
                                let highlightY = (yDis * 100) / height1;
                                // 设置画布
                                that.svg.style.transformOrigin = `${highlightX}% ${highlightY}% `;
                                // x 轴 y 轴的设置
                                anime({ targets: that.yLine, y1: yDis, y2: yDis, duation: 500 });
                                anime({ targets: that.xLine, x1: xDis, x2: xDis, duation: 500 });
                                // 数据展示
                                that.dataset.some((item) => {
                                    let [Year, Season] = anim.animatables[0].target.dataset.name.split("-");
                                    if (item.Year == Year && item.Season.toLowerCase() === Season) {
                                        that.tooltips = item;

                                        that.textAlert(that.text, `${item.Year}年${item.主办城市}${Season == "winter" ? "冬季" : "夏季"}奥运会`, [x, y]);
                                        that.textAlert(that.text2, `${item.举办国家} ${item.主办城市}`, [x, y]);
                                        return true;
                                    }
                                    return false;
                                });
                            }
                            // 设置进度条
                            that.progress = 3 * (index + 1);
                        },
                    });
                });
            },
            textAlert(which, what, where) {
                which.innerHTML = what;
                which.setAttribute("x", where[0]);
                which.setAttribute("y", where[1]);
            },
            pauseAndPlay() {
                this.timeline[this.timeline.paused ? "play" : "pause"]();
            },
            formatTooltip(val) {
                return this.points[val / 3 - 1].year + (this.points[val / 3 - 1].season == "summer" ? "夏" : "冬");
            },
            SVGScaleChange(val) {
                this.svg.style.transform = `scale(${val})`;
            },
            timeControl(val) {
                this.timeline.restart();
                this.timeline.pause();
                let toTime = (val - 1) * 1000 - 1000;
                let that = this;

                [...Array(2000 / 100).keys()].forEach((i, index) => {
                    setTimeout(() => {
                        that.timeline.seek(toTime + i * 100);
                    }, 100 * index);
                });
            },
        },
    };
</script>
<style scoped lang="less">
    .Box {
        background-color: #00173e;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        .tooltips {
            position: absolute;
            bottom: 10%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            left: 10px;
            height: 200px;
            width: 400px;
            color: aliceblue;
            background-color: #9797977a;
            border-radius: 10px;
            .btn {
                box-shadow: 0 0 #fff;
                font-size: 32px;
                cursor: pointer;
                font-weight: bolder;
            }
        }
        .scale {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
        }
        .slider {
            flex: 1;
            max-width: 70%;
        }
        .link {
            color: #fafafa;
            transition: color 0.5s;
            &:hover {
                color: rgb(248, 199, 248);
            }
        }
    }
</style>
<style>
    #WorldMap {
        width: 90vw;
    }
    #WorldMap > svg {
        transition: all 2s;
        transform: scale(5);
    }
    #WorldMap line {
        transition: all 2s;
    }
    #WorldMap .Text {
        font-size: 12px;
        fill: #ffffff;
        transform: scale(0.25);
        transition: all 0.5s;
        text-anchor: middle;
        dominant-baseline: middle;
        transform: translateY(-1em) scale(0.4);
    }
    #WorldMap .Text2 {
        font-size: 12px;
        fill: #ffffff;
        transform: scale(0.25);
        transition: all 0.5s;
        text-anchor: middle;
        dominant-baseline: middle;
        transform: translateY(1em) scale(0.4);
    }

    svg * {
        transform-box: fill-box !important;
        transform-origin: center center;
    }
</style>
